<template>
  <div class="login_container">
    <!--  -->
    <div class="login_box">
      <!--  -->
      <div class="login_img">
        <img src="../assets/logo.png" />
      </div>

      <!--  -->
      <el-form
        :model="loginForm"
        ref="loginFormRef"
        :rules="loginFormRules"
        label-width="0px"
        class="login_form"
      >
        <!--  -->
        <el-form-item prop="username">
          <el-input
            type="text"
            v-model="loginForm.username"
            placeholder=""
            clearable
            prefix-icon="el-icon-user-solid"
          ></el-input>
        </el-form-item>
        <!--  -->
        <el-form-item prop="password">
          <el-input
            type="password"
            v-model="loginForm.password"
            placeholder=""
            clearable
            prefix-icon="el-icon-key"
          ></el-input>
        </el-form-item>
        <!--  -->
        <el-form-item class="btns">
          <el-button type="primary" @click="login"></el-button>
          <el-button @click="resetForm('loginFormRef')"></el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      //
      loginForm: {
        username: "",
        password: ""
      },
      //
      loginFormRules: {
        //
        username: [
          { required: true, message: "", trigger: "blur" },
          { min: 3, max: 10, message: " 3  10 ", trigger: "blur" }
        ],
        //
        password: [
          { required: true, message: "", trigger: "blur" },
          { min: 6, max: 15, message: " 6  15 ", trigger: "blur" }
        ]
      }
    };
  },
  methods: {
    //
    login() {
      this.$refs.loginFormRef.validate(async valid => {
        if (!valid) return;
        const { data: res } = await this.$http.post("login", this.loginForm);
        console.log(res);
        if (res.meta.status !== 200) return this.$message.error(res.meta.msg);
        this.$message.success("");
        /**
         * 1.tokensessionStorage
         *   API
         *   tokentokensessionStorage
         */
        window.sessionStorage.setItem("vuetoken", res.data.token);

        /**
         * 
         */
        this.$router.push("/home");
      });
      // this.$refs.loginFormRef.validate(valid => {
      //   if (!valid) return;
      //   this.$http.post("login", this.loginForm)
      //   .then(res=>{
      //     if(res.meta.status === 200){
      //     }
      //   }).catch(error=>{
      //     console.log(error)
      //   }).finally(aa=>{
      //     console.log("111111")
      //   });
      // });
    },
    //
    resetForm(formName) {
      this.$refs[formName].resetFields();
    }
  }
};
</script>

<style lang="less" scoped>
.login_container {
  background-color: #2b4b6b;
  height: 100%;

  .login_box {
    width: 450px;
    height: 300px;
    background-color: white;
    border-radius: 5px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
}

.login_img {
  width: 130px;
  height: 130px;
  border-radius: 50%;
  background-color: white;
  position: absolute;
  left: 50%;
  transform: translate(-50%, -50%);
  border: 1px solid #eee;
  padding: 10px;
  box-shadow: 0 0 10px #eee;
  img {
    width: 100%;
    height: 100%;
    border-radius: 50%;
    background-color: #eeeeee;
  }
}

.login_form {
  width: 100%;
  position: absolute;
  bottom: 0;
  padding: 0 20px;
  box-sizing: border-box;

  .btns {
    display: flex;
    justify-content: flex-end;
  }
}
</style>
