<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!--  22-->
    <el-card>
      <el-table :data="dataList" border stripe  height="440" style="width: 100%">
        <el-table-column type="index"></el-table-column>
        <el-table-column prop="authName" label=""></el-table-column>
        <el-table-column prop="path" label=""></el-table-column>
        <el-table-column prop="level" label="">
          <template slot-scope="scope">
            <el-tag v-if="scope.row.level === '0'"></el-tag>
            <el-tag type="success" v-else-if="scope.row.level === '1'"></el-tag>
            <el-tag type="warning" v-else></el-tag>
          </template>
        </el-table-column>
      </el-table>
    </el-card>
  </div>
</template>

<script>
export default {
  data() {
    return {
      // 
      dataList: []
    }
  },
  created() {
    this.getRightsJson()
  },
  methods: {
    async getRightsJson() {
      const { data: res } = await this.$http.get('rights/list')
      console.log(res)
      if (res.meta.status != 200) {
        return this.$message.error('')
      }
      this.dataList = res.data
    }
  }
}
</script>

